<template>
  <div id="index">
    <!-- 顶部导航/搜索框 start -->
		<search-header :hasBack="hasBack">
			<router-link :to="{ name : 'Index'}" slot="icon">
				<div class="message" @click="toMessage">
					<img src="../../assets/common_btn_message@2x.png"/>
					<span class="point" v-if="false">0</span>
				</div>
			</router-link>
		</search-header>
		
    <!--<div class="top">
      搜索框/导航条
    </div>-->
    <div class="remind-info" @click="toEdit" v-if="hasSeted">
      完善个人信息可获得鸟蛋
      <img src="../../assets/home/home_btn_double_arrow@3x.png">
    </div>
    <!-- 顶部导航/搜索框 end -->
    <!-- 图片轮播 start -->
    <div style="min-height: 0.8rem;">
    	
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(item, index) in banner" :key="index">
        <img :src="item.imagePath" class="swipe-img"  alt="" @click="toActivity(item)"/>
      </van-swipe-item>
    </van-swipe>
    </div>
    <!-- 图片轮播 end -->
    <!-- 公告 start -->
    <div class="notice">
      <div class="notice-ico fl-left"></div>
      <div class="notice-list fl-left">
        <ul :class="{'notice-animate':animate}" @click="linkTo('Notice')">
          <li class="text-over" v-for="(item,index) in notice" v-text="item.actionName"></li>
        </ul>
      </div>
    </div>
    <!-- 公告 end -->
    <!-- 广告 start -->
    <div class="index-ad" v-if="recommend.length >= 4">
      <img :src="recommend[0].imagePath" alt="" class="ad-img1 fl-left" @click="toCustomized()">
      <div class="index-ad-right fl-left">
        <img :src="recommend[1].imagePath" alt="" class="ad-img2" @click="linkTo('School')">
        <img :src="recommend[2].imagePath" alt="" class="ad-img3 fl-left" @click="toActivity(recommend[2])">
        <img :src="recommend[3].imagePath" alt="" class="ad-img3 fl-left" @click="linkTo('Gift')">
      </div>
    </div>
    <!-- 广告 end -->
    <div class="cell bg-fff">热门活动</div>
    <div class="activity-list">
      <img v-for="(item,index) in hotActivity" :src="item.imagePath" alt="" @click="toActivity(item)">
    </div>
  </div>
</template>

<script>
	import SearchHeader from '@/components/Header/SearchHeader'
  import md5 from 'blueimp-md5';
	
  export default {
    data () {
      return {
      	hasSeted:true,
      	hasBack:false,
        animate:false,
        banner:[],//轮播图
        notice: [],//公告
        recommend:[],//导航推荐
				hotActivity:[],
      }
    },
    components: {
	  	SearchHeader
	  },
    watch: {},
    created(){
      setInterval(this.noticeScroll,1500);
			this.getSetting();
      this.getAdList(1);
      this.getAdList(2);
      this.getAdList(3);
      this.getAdList(9);
//    this.getUserDetails()
//    this.getAdList(6);
//    this.getGoodsList();
//			this.$Api.untilSessionId().then(()=>{
//				console.log('1-1')
//				this.getArticleList();
//			})
    },
    methods: {
      /*公告滚动*/
      noticeScroll(){
        this.animate=true;
        setTimeout(()=>{
          this.notice.push(this.notice[0]);
          this.notice.shift();
          this.animate=false;
        },1000)
      },
			getUserDetails() {
				this.$Api.UserDetails(0).then((res) => {
					if(res.q.s == 0){
						localStorage.removeItem('UserDetails')
						let userStr = JSON.stringify(res.q.user)
						localStorage.setItem('UserDetails',userStr)
					}
				})
			},
      toActivity(item){
      	if(item.actionContent !=''){
	      	if(item.actionType == 1 || item.actionType == 2 || item.actionType == 3 || item.actionType == 8){
						window.location.href = item.actionContent
					}else if(item.actionType == 5 || item.actionType == 9){
						this.$router.push({name:'ArticleDetails',query:{id:item.actionContent,type:'article'}})
					}
      	}
      },
      toCustomized(){
        // console.log(item)
        this.$router.push({name:'Customized',query:{}})
      },
      getAdList(action){
        this.$Api.getAdList(action).then((res) => {
        	if(res.q.s == 0){
        		switch (action){
        			//1.轮播图
        			case 1:
        				this.banner = res.q.ads
        				break;
        			//2.导航推荐广告	
        			case 2:
        				this.recommend = res.q.ads
        				break;
        			//3.热门活动	
        			case 3:
        				this.hotActivity = res.q.ads
        				break;
        			case 9:
        				this.notice = res.q.ads
        			break;
        			default:
        				break;
        		}
        	}
        })
      },
//			getGoodsList(){
//				this.$Api.getGoodsList(0,1).then((res)=>{
//					console.log(res);
//				})
//			},
			toEdit(){
				this.$router.push({name:'Edit'})
				localStorage.setItem('hasSeted','hasSeted')
			},
			getSetting(){
				let flag = localStorage.getItem('hasSeted')
				console.log(flag)
				if(flag != null){
					if(flag == 'hasSeted'){
						this.hasSeted = false
					}
				}
			},
			toMessage(){
//				this.$Api.UserLogout().then((res)=>{
//					console.log(res)
//				})
			},
			
			getArticleList(){
				this.$Api.getArticleList(1).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.notice = res.q.articles
					}
				})
			},
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #index{
    background-color: #f0f0f0;
    .message{
      >img{
        width:0.61rem;
        height: 0.6rem;
      }
    	position: relative;
	    display: inline-block;
	    margin-left: 0.4rem;
	    width: 0.613rem;
	    height: 0.6rem;
      .point{
        position: absolute;
        right: -0.2rem;
        top: -0.2rem;
        padding: 0 0.1rem;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        font-size: 0.32rem;
        background: #fe4436;
        color: #fff;
        border-radius: 0.4rem;
      }
    }
    .top{
      width: 100%;
      height: 1.71rem;
      background-image: linear-gradient(135deg,#fe9bba 0%,#de7fef 100%);
    }
    .remind-info{
      width: 100%;
      height: 0.8rem;
      line-height: 0.8rem;
      padding: 0 0.41rem;
      background: rgba(0,0,0,0.7);
      position: absolute;
      // top: 1.71rem;
      left: 0;
      z-index: 10;
      color: #fff;
      font-size: 0.37rem;
      img{
        height: 40%;
        position: absolute;
        right: 0.41rem;
        top: 30%;
      }
    }

    .van-swipe__indicator--active {
      background-color: #de7fef;
    }
    .swipe-img {
      width: 100%;
      height: 4rem;
    }
    .notice{
      height: 0.93rem;
      background-color: #fff;
      border-top: 1px #ccc solid;
      border-bottom: 1px #ccc solid;
      overflow: hidden;
      .notice-ico{
        width: 1.63rem;
        height: 0.43rem;
        background: url("../../assets/home/home_img_notice@3x.png") no-repeat center;
        background-size: 0.84rem auto;
        padding: 0 0.4rem;
        margin-top: 0.27rem;
        border-right: 1px #ccc solid;
      }
      .notice-list li{
        width: 8.36rem;
        height: 0.93rem;
        line-height: 0.93rem;
        /*line-height: 0.43rem;*/
        font-size: 0.43rem;
        color: #333;
        padding: 0 0.43rem;
        /*padding: 0.27rem 0.43rem;*/
      }
      .notice-animate{
        transition: all 1s;
        margin-top: -0.93rem;
      }
    }
    .index-ad{
      width: 100%;
      height: 5.68rem;
      border-bottom: 1px #ccc solid;
      /*padding: 0.01rem 0 0 0.01rem;*/
      padding: 0.11rem;
      img{
        /*margin-top: 0.10rem;
        margin-left: 0.10rem;*/
        display: block;
      }
      .index-ad-right{
        /*width: 5.57rem;*/
        padding-left: 0.11rem;
        width: 60%;
        height: 100%;
      }
      .ad-img1{
        width: 40%;
        height: 100%;
        /*width: 4.11rem;*/
        /*height: 5.44rem;*/
      }
      .ad-img2{
        /*width: 5.53rem;*/
        width: 100%;
        height: 2.67rem;
        margin-bottom: 0.11rem;
      }
      .ad-img3{
        /*width: 2.68rem;*/
        width: 50%;
        height: 2.67rem;
        &:nth-of-type(2){
          padding-right: 0.11rem;
        };
      }
    }
    .activity-list{
      padding-bottom: 1.31rem;
      >img{
        display: block;
      	width: 100%;
        height:4.0rem;
        margin-bottom: 0.27rem;
        
      }
    }
  }

</style>



